import React, { useState } from 'react';
import translit from 'iso_9';
import TextField from '@mui/material/TextField';

  function App() {
    const [input, setInput] = useState('');
    const [output, setOutput] = useState('');
    const handleInputChange = event => {
      setInput(event.target.value);
    }

    const transliterate = event => {
      setOutput(translit(input, 1));
    }
    return (
      <div className="App">
        <header>
      </header>
          <div style={{
            display: 'flex',
            width: '100%',
            minHeight: '200px'}}>
            <div style={{padding: '40px', display: 'flex'}}>
          <TextField label="Text to transliterate" variant="outlined" onChange={handleInputChange}/>
          </div>
          <div style={{padding: '40px'}}>
          <TextField label="Result" variant="outlined" value={output} inputProps={{readOnly: true}}/>
          </div>
          <div style={{padding: '40px'}}>
          <button onClick={transliterate}>Transliterate</button>
          </div>
          </div>
    </div>
  );
}

export default App;
